// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-bright-turquoise:            #01e4db;
$color-nero:                        #222222;
$color-gainsboro:                   #d9d9d9;
$color-sherpa-blue:                 #004845;
$color-matterhorn:                  #555555;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-tilda:                         "TildaSans", "Arial", sans-serif;
$ff-bahn:                          "Bahnschrift", "Arial", sans-serif;
$ff-open:                          "OpenSans-Regular", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
